import { NavConfig } from "@config/navigation/config"
import { Redirect } from "@components/Redirect"
import { lazy, Suspense } from "react"
import { IndexRouteObject, NonIndexRouteObject, Outlet } from "react-router"
import { Navbar } from "@components/Navbar"
import { Footer } from "@components/Footer"

type IIndexRouteObject = Omit<NonIndexRouteObject, "children"> & {
  children?: IRouteObject[]
}
type IRouteObject = IndexRouteObject | IIndexRouteObject

const _404NotFound = lazy(() => import("@shared/components/_404NotFound"))
const AboutUs = lazy(() => import("@views/OurOrganisation"))
const BalVikas = lazy(
  () => import("@views/ImportantInitiativesPrograms/BalVikas")
)
const CeilingOnDesire = lazy(
  () => import("@views/ImportantInitiativesPrograms/CeilingOnDesire")
)
const CleaningAndSanitisation = lazy(
  () => import("@views/ImportantInitiativesPrograms/CleaningAndSanitisation")
)
const Dashboard = lazy(() => import("@views/Dashboard"))
const DisasterManagement = lazy(
  () => import("@views/ImportantInitiativesPrograms/DisasterManagement")
)
const DivineAndDirective = lazy(() => import("@views/DivineAndDirective"))
const GramaSeva = lazy(
  () => import("@views/ImportantInitiativesPrograms/GramaSeva")
)
const Insignias = lazy(() => import("@views/Insignias"))
const OurNationalInitiatives = lazy(
  () => import("@views/ImportantInitiativesPrograms/OurNationalInitiatives")
)
const OurWingsAndActivities = lazy(() => import("@views/OurWingsAndActivities"))
const ReachOut = lazy(() => import("@views/ReachOut"))
const SkillDevelopment = lazy(
  () => import("@views/ImportantInitiativesPrograms/SkillDevelopment")
)
const UnitsPage = lazy(() => import("@views/UnitsPage"))
const VidyaJyothi = lazy(
  () => import("@views/ImportantInitiativesPrograms/VidyaJyothi")
)

const LazyElementsForOurorganisation: { [key: string]: React.ReactNode } =
  Object.freeze({
    "the-organisation-about-us": <AboutUs />,
    "the-organisation-wings-activities": <OurWingsAndActivities />,
    "the-organisation-divine-directives-guidelines": <DivineAndDirective />,
    "the-organisation-insignias": <Insignias />,
    "the-organisation-units": <UnitsPage />
  })
const LazyElementsForInitiativesProgram: { [key: string]: React.ReactNode } =
  Object.freeze({
    "important-initiatives-and-programs-bal-vikas": <BalVikas />,
    "important-initiatives-and-programs-vidya-jyothi": <VidyaJyothi />,
    "important-initiatives-and-programs-grama-seva": <GramaSeva />,
    "important-initiatives-and-programs-disaster-management": (
      <DisasterManagement />
    ),
    "important-initiatives-and-programs-ceiling-on-desire": <CeilingOnDesire />,
    "important-initiatives-and-programs-cleaning-and-sanitation": (
      <CleaningAndSanitisation />
    ),
    "important-initiatives-and-programs-skill-development": (
      <SkillDevelopment />
    ),
    "important-initiatives-and-programs-our-national-initiatives": (
      <OurNationalInitiatives />
    )
  })

export const routes: IRouteObject[] = [
  {
    id: "base-ui",
    path: "/",
    element: (
      <Suspense fallback={<div>Loading...</div>}>
        <Navbar />
        <Outlet />
        <Footer />
      </Suspense>
    ),
    children: [
      {
        id: "redirect-home",
        index: true,
        element: <Redirect path="/home" key={"home-redirect"} />
      },
      {
        id: NavConfig["home"].id,
        path: NavConfig["home"].path,
        element: <Dashboard />
      },
      {
        id: NavConfig["important-initiatives-and-programs"].id,
        path: NavConfig["important-initiatives-and-programs"].path,
        children: NavConfig["important-initiatives-and-programs"].children?.map(
          (item, index) => ({
            id: item.id,
            path: item.path,
            index: index === 0,
            element: LazyElementsForInitiativesProgram?.[item.id]
          })
        )
      },
      {
        id: NavConfig["the-organisation"].id,
        path: NavConfig["the-organisation"].path,
        children: NavConfig["the-organisation"].children?.map(
          (item, index) => ({
            id: item.id,
            path: item.path,
            index: index === 0,
            element: LazyElementsForOurorganisation?.[item.id]
          })
        )
      },
      {
        id: NavConfig["reach-out-to-us"].id,
        path: NavConfig["reach-out-to-us"].path,
        element: <ReachOut />
      },
      {
        id: "not-found-base",
        path: "*",
        element: <_404NotFound />
      }
    ]
  },
  {
    id: "not-found-all",
    path: "*",
    element: <_404NotFound />
  }
]
